:root {
  --primary-color: #4a90e2;
  --secondary-color: #3d7eaa;
  --disabled-color: #444;
  --hover-color: #1f4f7f;
}

@-webkit-keyframes fade-in {
  0% {
    opacity: 0;
    -webkit-transform: translateY(20px);
            transform: translateY(20px);
  }

  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes fade-in {
  0% {
    opacity: 0;
    -webkit-transform: translateY(20px);
            transform: translateY(20px);
  }

  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

body {
  font-family: 'Roboto', sans-serif;
  /* Use the Roboto font for the entire document */
}

.Hangman {
  width: 400px;
  color: black;
  text-align: center;
  zoom: 1.3;
  -webkit-animation: fade-in 1s ease-out;
          animation: fade-in 1s ease-out;
}

.Hangman img {
  border: 10px solid white;
  -webkit-box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2), 0 0 40px rgba(0, 0, 0, 0.1) inset;
          box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2), 0 0 40px rgba(0, 0, 0, 0.1) inset;
  -webkit-transition: -webkit-transform 0.3s ease-in-out, -webkit-box-shadow 0.3s ease-in-out;
  transition: -webkit-transform 0.3s ease-in-out, -webkit-box-shadow 0.3s ease-in-out;
  -o-transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
  transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
  transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out, -webkit-box-shadow 0.3s ease-in-out;
  -webkit-animation: fade-in 1s ease-out;
          animation: fade-in 1s ease-out;
}

.Hangman img:hover {
  -webkit-transform: scale(1.05);
      -ms-transform: scale(1.05);
          transform: scale(1.05);
  -webkit-box-shadow: 0 15px 35px rgba(0, 0, 0, 0.3), 0 0 40px rgba(0, 0, 0, 0.2) inset;
          box-shadow: 0 15px 35px rgba(0, 0, 0, 0.3), 0 0 40px rgba(0, 0, 0, 0.2) inset;
}

.Hangman h1 {
  font-size: 2.5rem;
  font-weight: 300;
  /* Use a lighter font-weight for a modern feel */
  margin: 0.1em 0;
  color: var(--primary-color);
  -webkit-animation: fade-in 1s ease-out;
          animation: fade-in 1s ease-out;
}

.Hangman-word {
  letter-spacing: 0.8em;
  margin: 0.4em -0.8em 0.2em 0;
  font-size: 2rem;
  color: var(--secondary-color);
  -webkit-transition: color 0.3s ease-in-out;
  -o-transition: color 0.3s ease-in-out;
  transition: color 0.3s ease-in-out;
}

.Hangman-btns {
  text-align: center;
  display: inline-block;
  margin-top: 1em;
  -webkit-animation: fade-in 1s ease-out;
          animation: fade-in 1s ease-out;
}

.Hangman button {
  border: none;
  font-size: 1rem;
  cursor: pointer;
  width: 30px;
  margin: 5px 2px;
  letter-spacing: 1px;
  outline: none;
  background: var(--primary-color);
  color: white;
  border-radius: 5px;
  padding: 6px 0;
  -webkit-transition: background-color 0.3s ease-in-out, -webkit-transform 0.2s ease-in-out, -webkit-box-shadow 0.2s ease-in-out;
  transition: background-color 0.3s ease-in-out, -webkit-transform 0.2s ease-in-out, -webkit-box-shadow 0.2s ease-in-out;
  -o-transition: background-color 0.3s ease-in-out, transform 0.2s ease-in-out, box-shadow 0.2s ease-in-out;
  transition: background-color 0.3s ease-in-out, transform 0.2s ease-in-out, box-shadow 0.2s ease-in-out;
  transition: background-color 0.3s ease-in-out, transform 0.2s ease-in-out, box-shadow 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out, -webkit-box-shadow 0.2s ease-in-out;
  -webkit-animation: fade-in 1s ease-out;
          animation: fade-in 1s ease-out;
}

.Hangman button:hover {
  background-color: var(--hover-color);
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
  -webkit-box-shadow: 0 6px var(--secondary-color);
          box-shadow: 0 6px var(--secondary-color);
}

.Hangman button:disabled {
  background: var(--disabled-color);
  color: #999;
  -webkit-box-shadow: 0 6px #666;
          box-shadow: 0 6px #666;
  cursor: not-allowed;
}

.Hangman-btn-restart {
  border: none;
  font-size: 1rem;
  cursor: pointer;
  margin: 5px 2px;
  letter-spacing: 1px;
  outline: none;
  background: var(--primary-color);
  color: white;
  border-radius: 5px;
  padding: 12px 20px;
  /* Increase padding to make it larger */
  -webkit-transition: background-color 0.3s ease-in-out, -webkit-transform 0.2s ease-in-out, -webkit-box-shadow 0.2s ease-in-out;
  transition: background-color 0.3s ease-in-out, -webkit-transform 0.2s ease-in-out, -webkit-box-shadow 0.2s ease-in-out;
  -o-transition: background-color 0.3s ease-in-out, transform 0.2s ease-in-out, box-shadow 0.2s ease-in-out;
  transition: background-color 0.3s ease-in-out, transform 0.2s ease-in-out, box-shadow 0.2s ease-in-out;
  transition: background-color 0.3s ease-in-out, transform 0.2s ease-in-out, box-shadow 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out, -webkit-box-shadow 0.2s ease-in-out;
  -webkit-animation: fade-in 1s ease-out;
          animation: fade-in 1s ease-out;
}

.Hangman-btn-restart:hover {
  background-color: var(--hover-color);
  -webkit-transform: scale(1.05);
      -ms-transform: scale(1.05);
          transform: scale(1.05);
  -webkit-box-shadow: 0 6px var(--secondary-color);
          box-shadow: 0 6px var(--secondary-color);
}


